.live-bar {
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: gray;
  transition: background-color 0.3s ease, box-shadow 0.3s ease, opacity 0.3s ease;
  opacity: 0.3;
}

/* Green glowing effect when API is working */
.live-bar.active {
  opacity: 1;
  background-color: #0EE41D;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.8);
  animation: blink 0.00001s infinite alternate;
}

/* Blinking red effect when API is down */
.live-bar.error {
  opacity: 1;
  background-color: #CC1203;
  box-shadow: 0 0 10px rgba(255, 0, 0, 0.8);
  animation: blink 0.00001s infinite alternate;
}

@keyframes blink {
  from { opacity: 1; }
  to { opacity: 0.3; }
}



