/* Handle of the switch (transformed into a button) */
.custom-handle {
  top: 2.4%;
  position: absolute;
  transform: translateY(-50%);
  width: 20px; /* Increased size to make it more button-like */
  height: 20px;
  border-radius: 30px; /* Full rounded corners for a button-like look */
