/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.home {
  font-family: 'Roboto', sans-serif;
  color: #333;
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  margin-top: 20px;
}

.sections-container {
  display: grid;
  grid-template-columns: 1fr 3fr; /* Left narrow, right wide */
  grid-template-rows: auto 1fr;  /* Top auto height, bottom fills */
  gap: 20px;
  height: 100%;
  width: 100%;
}

/* Top section spans full width */
.graf-link-button-section {
  grid-column: 1 / 3; /* Span across both columns */
  width: 100%; /* Full width */
  height: 40px; /* Full height */
  border-radius: 12px;
 /*  border: 0.5px solid silver; */
  display: flex;
  justify-content: left;
  align-items: center;
   /* transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; */
   padding: 10px;
  background-color: #222;
  z-index: 0;
}

/* Logo wrapper section inside top bar */
.logo-wrapper-section {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
}

/* Example logo styles */
.logo-wrapper {
  cursor: pointer;
}

.spinner-ring.active {
  outline: 2px solid #007bff; /* highlight active */
  border-radius: 50%;
}

.logo-center img.logo {
  height: 30px;
  width: 30px;
  object-fit: contain;
}

/* Left side vertical split */
.left-side {
  grid-column: 1 / 2;
  grid-row: 2 / 3;
  display: grid;
  grid-template-rows: 1fr 1fr; /* top and bottom halves */
  gap: 20px;
  height: 100%;
}

/* Left top buttons */
.buttons-section-top {
  background-color: #e8e8e8;
  border-radius: 6px;
  padding: 10px;
  overflow-y: auto;
}

/* Left bottom buttons / content */
.buttons-section-bottom {
  background-color: #dcdcdc;
  border-radius: 6px;
  padding: 10px;
  overflow-y: auto;
}

/* Right side PodStatus full height */
