  grid-column: 2 / 3;
  grid-row: 2 / 3;
  background-color: #ccc;
  border-radius: 6px;
  padding: 10px;
  height: 100%;
  overflow-y: auto;
}

/* Embed section when active embed */
.next-embed-section {
  grid-column: 1 / -1;
  grid-row: 2 / 3;
  padding: 20px;
  background: #fafafa;
  border-radius: 6px;
  overflow-y: auto;
  height: 100%;
}



@media (max-width: 768px) {
  .sections-container {
    grid-template-columns: 1fr; /* Stack sections in a single column */
    grid-template-rows: auto auto auto auto auto auto; /* Stack sections vertically */
