/* Animation for dropdown effect with 5px margin */
@keyframes slideDown {
  from {
    transform: translateY(-100%) /* Starts off-screen, 1000% above */;
    opacity: 0;
  }
  to {
    transform: translateY(0); /* Ends 5px below its initial position */
    opacity: 1;
  }
}

/* Apply animation to the embed section */
.next-embed-section {
  animation: slideDown 0.8s ease-in-out forwards;
}

